.nav_wrapper {
  position: relative;
  border-bottom: 1px solid #b2b2b2;
  min-height: 127px;
  background-color: #fff;
  z-index: 10;
}

.nav_left {
  text-align: right;
}

.nav_right {
  text-align: left;
}

.nav_logo {
  width: 90px;
  margin-left: 50px;
  margin-top: 40px;
  position: relative;
}

.nav_logo:before {
  position: absolute;
  content: '';
  height: 7px;
  width: 100px;
  background-color: #000;
  bottom: -32px;
  left: -15px;
}

.nav li {
  display: inline-block;
}

.nav li a {
  text-align: center;
  margin: 50px 5px;
  display: block;
  text-transform: uppercase;
  font-size: 14px;
  color: #000;
  padding: 0 15px;
  position: relative;
}

.nav li a::before, .nav li a::after {
  position: absolute;
  content: '';
  top: 50%;
  width: 10px;
  height: 1px;
  margin-top: -1px;
  background: #000;
  opacity: 0;
  transition: opacity 0.3s, transform 0.3s;
}

.nav li a::before {
  left: 0;
  transform: translateX(-100%);
}

.nav li a::after {
  right: 0;
  transform: translateX(100%);
}

.nav li a:active::before, .nav li a:active::after, .nav li a:hover::before, .nav li a:hover::after {
  transform: translate(0);
  opacity: 1;
}

.top-line {
