.first-screen_text {
  position: absolute;
  top: 129px;
  left: calc(38.5%);
}

.first-screen_text h1 {
  font-family: "NewAthenaUnicode", sans-serif;
  font-size: 48px;
  position: relative;
  font-weight: 400;
  margin-top: 40px;
  text-transform: uppercase;
}

.first-screen_text h1:before {
  position: absolute;
